html {
  font-size: 100%;
  box-sizing: border-box;
  position: relative;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin-top: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--outfit);
  font-size: 1rem;
  line-height: 1.5;
  color: var(--text1);
  background-color: var(--bg);
}
@media (min-width: 34.375em) {
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    min-height: 100vh;
    margin: 8rem 0;
  }
}

h1, h2 {
  font-family: var(--young-serif);
  margin-bottom: 1.5rem;
}

h1 {
  font-weight: 400;
}

h2 {
  color: var(--accent);
  font-size: 1.75rem;
  font-weight: 400;
}

ol > li::marker {
  font-weight: bold;
}

li {
  padding-left: 1rem;
}
li span {
  font-weight: 700;
}

li::marker {
  color: var(--accent);
}

.preparation li::marker {
  color: var(--prep-title);
}

.wrapper {
  max-width: 46rem;
  width: calc(100vw - 76px);
  margin-inline: auto;
}
@media (max-width: 34.375em) {
  .wrapper {
    width: 100vw;
  }
}

.separator {
  width: 100%;
  height: 1px;
  background-color: var(--separator);
  margin: 2rem 0;
}

.recipe {
  background-color: var(--container-bg);
  padding: 2.5rem 2rem;
}
@media (min-width: 34.375em) {
  .recipe {
    padding: 2.5rem;
    border-radius: 0 0 1.5rem 1.5rem;
  }
}
.recipe__title {
  color: var(--recipe-title);
}
.recipe__description {
  margin-bottom: 2rem;
}

.nutrition p {
  margin-bottom: 1.5rem;
}
.nutrition .separator {
  margin: 0.75rem 0;
}
.nutrition__property {
  display: flex;
}
.nutrition__property span {
  flex: 1;
  text-align: left;
}
.nutrition__property span:nth-child(2) {
  color: var(--accent);
  font-weight: 700;
}

h1, h2, h3 {
  margin-top: 0;
}

h1 {
  font-size: 2.25rem;
  font-size: clamp(2.25rem, 2.01rem + 1.02vw, 2.5rem);
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --prep-title: hsl(332, 51%, 32%);
  --prep-bg: hsl(330, 100%, 98%);
  --bg:hsl(30, 54%, 90%);
  --recipe-title: hsl(24, 5%, 18%);
  --text1: hsl(30, 10%, 34%);
  --accent: hsl(14, 45%, 36%);
  --separator: hsl(30, 18%, 87%);
  --container-bg: hsl(0, 0%, 100%);
}

:root {
  --outfit: "Outfit", sans-serif;
  --young-serif: "Young Serif", serif;
}

header {
  width: 100%;
  background-color: var(--container-bg);
}
@media (min-width: 34.375em) {
  header {
    padding: 2.5rem 2.5rem 0 2.5rem;
    border-radius: 1.5rem 1.5rem 0 0;
  }
}
header img {
  display: block;
  width: 100%;
}
@media (min-width: 34.375em) {
  header img {
    border-radius: 0.75rem;
  }
}

.preparation {
  border-radius: 0.75rem;
  padding: 1.5rem;
  margin-bottom: 2rem;
  background-color: var(--prep-bg);
}
.preparation__title {
  color: var(--prep-title);
}